
import { Injectable } from '@angular/core';


@Injectable({
    providedIn: 'root'
})
/*

*/
export class Storage_LoginInfo {

    private userinfo: string = "login_userinfo";
    SetInfo(data: any) {
        window.localStorage.setItem(this.userinfo, data);
    }
    GetUserInfo() {
        var data = window.localStorage.getItem(this.userinfo);
        return JSON.parse(data);
    }
    //userid
    GetSystemUserId() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.systemuserid;
        return null;
    }
    //
    GetName() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.name;
        return null;
    }
    //
    GetNickName() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.nickname;
        return null;
    }
    //
    GetGender() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.gender;
        return null;
    }
    //
    GetAccount() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.account;
        return null;
    }
     //
     GetPhone() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.phone;
        return null;
    }
    //
    GetCardid() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.cardid;
        return null;
    }
    //
    GetEmail() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.email;
        return null;
    }
    //
    GetBirthday() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.birthday;
        return null;
    }
    //
    GetMarriagestatus() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.marriagestatus;
        return null;
    }
    //
    GetSignature() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.signature;
        return null;
    }


    //
    GetCode() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.code;
        return null;
    }
    //
    GetMemberid() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.memberid;
        return null;
    }


}


